    transform: translateY(-50%);
    width: 34px;
    height: 34px;
    background: url(../images/icons/arr-left.png);
  }
}
@media only screen and (min-width: 768px) {
  .prev {
    left: -15px;
  }
}
@media only screen and (min-width: 992px) {
  .prev {
    left: -20px;
  }
}
@media only screen and (min-width: 1200px) {
  .prev {
    left: 0;
  }
}
.next {
  display: none;
}
@media only screen and (min-width: 360px) {
  .next {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -7px;
    width: 34px;
    height: 34px;
    background: url(../images/icons/arr-right.png);
  }
}
@media only screen and (min-width: 768px) {
  .next {
    right: -15px;
  }
}
@media only screen and (min-width: 992px) {
  .next {
    right: -20px;
  }
}
@media only screen and (min-width: 1200px) {
  .next {
    right: 0;
  }
}
